import React, {useState} from 'react';

export const Pops = (props)=>{

    const {mensage} = props;
    const [visible , setVisible] = useState(true);
    const style = {
                    color:'green',
                    width:'100%',
                    textAlign:'center'
    }
    if(visible){
        setTimeout(()=>{
            setVisible(false);
        },3000)
        return (<span style={style}>{mensage}</span>)
    }else{
        return <></>
    }

}