import react, {useState} from 'react';
import {Login} from './components/login';
import {FormOpera} from './components/FormOpera';

export function Operarios() {

  // state
    const [login , setLogin] = useState(false);
    const [authentication , setAuthentication] = useState(null);
    const [usuario , setUsuario] = useState(null);

  // colores
    const gradiant    = {
                        a:'blue',
                        b:'royalblue'
    }
    const colorTitle  = {
                        background : `linear-gradient(-90deg, ${gradiant.a},${gradiant.b}, white)`,
                        color : 'white'
    }
    const colorButton   = {
                          background : 'transparent',
                          color : 'royalblue',
    }
  // CSS
    const operariosCss  = ()=>{

      return(`
              .Operarios{
                          display : flex;
                          flex-direction : column;
                          justify-items : center;
                          align-items : center;
                          max-width : 500px;
                          padding: 20px;
                          min-height : 60%;
                          min-width : 30%;
              }
              @media(max-width: 500px){
                  .Operarios{
                    width : 100%;
                    height: 100%;
                    padding:0px;
                  }
              }
              @media(min-width: 500px){
                .Operarios{
                  width : 60%;
                  height: 30%;
                  padding:20px;
                }
            }
      `)

                  
    }

    if(login){
      return(
        <div className={`Operarios`}>
        <style>{operariosCss()}</style>
  
        <FormOpera
            colorTitle={colorTitle}
            colorButton={colorButton}
            usuario={usuario}>

        </FormOpera>
        </div>
      )
    }else{
      return (
    
        <div className={`Operarios`}>
        <style>{operariosCss()}</style>
  
            <Login
                colorTitle={colorTitle}
                colorButton={colorButton}
                setLogin={setLogin}
                setAuthentication={setAuthentication}
                authentication={authentication}
                setUsuario={setUsuario}>
            </Login>
        </div>
      
  
    );
    }


}


