import react from 'react';
import {handleLogin} from '../handles/handleLogin';
import {ReactComponent as RealPro} from './../svg/realPro.svg';


export const Login =(props)=>{

    // props
    const { 
        colorTitle,
        colorButton,
        setLogin,
        setAuthentication,
        authentication,
        setUsuario
    }  
                    = props;

    // nombre componente
    const name = 'Login';

    // CSS

    const containerCss = ()=>{

        return (`
                    .container-${name}{
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: stretch;
                        border: 1px solid black;
                        width: 100%;
                        height: 100%;
                        background-color:whitesmoke;
                        flex : 1;
                        position: relative;
                        overflow: hidden;
                    }        
        `)
    }
    const titleCss     = ()=>{

        return(`

                .title-${name}{
                                display: flex;
                                align-items: center;
                                justify-content:center;
                                width: 100%;
                                height: 40px;
                                position: absolute;
                                top:0px;
                                left:0px;
                                margin:0;
                                background: ${colorTitle.background}  ;
                                color :  ${colorTitle.color}  ;
                }    
        `)
    }
    const inputCss     = ()=>{

        return(`

                .input-${name}{
                                border: none;
                                margin: 20px;
                                height: 35px;
                }    
        `)
    }
    const buttonCss    = ()=>{

        return(`

                .button-${name}{
                                    display : flex;
                                    justify-content: center;
                                    align-items: center;
                                    border: 1px solid ${colorButton.color};
                                    margin: 20px;
                                    height: 35px;
                                    color : ${colorButton.color};
                                    cursor: pointer;
                                    transition-property: background-color;
                                    transition-duration:0.3s;
                }
                .button-${name}:hover{
                                        background-color:${colorButton.color};
                                        color: white;
                }    
        `)
    }

    // HTML
    return(
        
            <form className={`container-${name}`}>
            <style>{
                    containerCss()+
                    titleCss()+
                    inputCss()+
                    buttonCss()
            }
            </style>

                <div 
                    className={`title-${name}`}>
                        <RealPro 
                        style={{width:'30px',height:'30px',position:'absolute',top:'5px',left:'10px'}}>
                        </RealPro>
                        Login
                </div>

                <input 
                    type = "text" 
                    className={`input-${name}`} 
                    placeholder="usuario"
                    name="usuario">
                </input>

                <input 
                    type="text" 
                    className={`input-${name}`} 
                    placeholder="contraseña"
                    name="contrasena">
                </input>

                {(authentication == false)? <span style={{color:'red',textAlign:'center'}}>Error  authentication</span> : <></>}

                <div 
                    className={`button-${name}`}
                    onClick={(e)=>handleLogin(e,setLogin,setAuthentication,setUsuario)}>
                    Connect
                </div>
            </form>
        
    )
}