export async function postAjax(url,data ,callback=console.log) {
    const objectAjax = {
            method: 'POST',
            headers: {'content-type': 'application/json'},
            body: JSON.stringify(data)
    }
    try{
        let res = await fetch(url,objectAjax);
        let resData = await res.json();
        if(resData.login !== undefined){
            callback(resData);
        }else{
            callback(resData);
        }
        return res.ok;
    }
    catch(e){console.error(e)}


}