import react,{useState, useEffect} from 'react';
import {handleEnviarDatos} from '../handles/handleEnviarDatos';
import {getDateNow} from '../assets/getDateNow';
import Logo from '../img/logo.png';
import {Pops} from '../components/Pops';
import {ReactComponent as RealPro} from './../svg/realPro.svg';


export const FormOpera =(props)=>{

    const [posicion , setPosicion] = useState();
    const [envio , setEnvio] = useState(false);
    const { colorTitle,
            colorButton,
            usuario
    } = props;
    const name = usuario;


    useEffect(()=>{
        if('geolocation' in navigator){
            console.log(true);
            navigator.geolocation.getCurrentPosition(async(position)=>{

                const geo = await {
                            latitud:position.coords.latitude,
                            longitud:position.coords.longitude

                }
                setPosicion(geo)

            })
        }
    },[])
    

        // CSS

        const containerCss = ()=>{

            return (`
                        .container-${name}{
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: stretch;
                            border: 1px solid black;
                            width: 100%;
                            height: 100%;
                            background-color:whitesmoke;
                            flex : 1;
                            position: relative;
                            overflow: hidden;
                        }        
            `)
        }
        const titleCss     = ()=>{
    
            return(`
    
                    .title-${name}{
                                    display: flex;
                                    align-items: center;
                                    justify-content:center;
                                    width: 100%;
                                    height: 40px;
                                    position: absolute;
                                    top:0px;
                                    left:0px;
                                    background: ${colorTitle.background}  ;
                                    color :  ${colorTitle.color}  ;
                    }    
            `)
        }
        const inputCss     = ()=>{
    
            return(`
    
                    .input-${name}{
                                    border: none;
                                    margin: 10px;
                                    height: 35px;
                    }    
            `)
        }
        const buttonCss    = ()=>{
    
            return(`
    
                    .button-${name}{
                                        display : flex;
                                        justify-content: center;
                                        align-items: center;
                                        border: 1px solid ${colorButton.color};
                                        margin: 20px;
                                        height: 35px;
                                        color : ${colorButton.color};
                                        cursor: pointer;
                                        transition-property: background-color;
                                        transition-duration:0.3s;
                    }
                    .button-${name}:hover{
                                            background-color:${colorButton.color};
                                            color: white;
                    }    
            `)
        }
        const mediaCss     = ()=>{

            return (`

                        .primero{
                            margin-top:60px;
                        }
                        @media(max-width:500px){
                            .container-${name}{
                                justify-content: flex-start;                               
                            }
                            .container-${name} .primero{
                                margin-top: 100px;
                            }
                            .button-${name}{
                                margin-top:150px;
                            }
                        }          
            
            `)
        }
    
    // HTML
    return(
<form className={`container-${name}`}>
            <style>{
                    containerCss()+
                    titleCss()+
                    inputCss()+
                    buttonCss()+
                    mediaCss()
            }
            </style>
                <div 
                    className={`title-${name}`}>
                    <RealPro 
                        style={{width:'30px',height:'30px',position:'absolute',top:'5px',left:'10px'}}>
                    </RealPro>
                    <p>
                    {`Registro de entrada de : ${name}`}
                    </p>
                </div>

                <input 
                    type = "text" 
                    hidden = {true}
                    className={`input-${name} ` } 
                    value={usuario}
                    name="usuario">
                </input>

                <input 
                    type = "text" 
                    hidden = {true}
                    className={`input-${name} ` } 
                    value={JSON.stringify(posicion)}
                    name="geo">
                </input>

                <input 
                    type = "text" 
                    hidden = {true}
                    className={`input-${name} ` } 
                    value={getDateNow(3)}
                    name="hora">
                </input>


                <input 
                    type = "date" 
                    className={`input-${name} primero` } 
                    placeholder="Fecha"
                    name="entrada">
                </input>

                <input 
                    type = "text" 
                    className={`input-${name}` } 
                    placeholder="Hora entrada.."
                    name="entrada">
                </input>

                <input 
                    type="text" 
                    className={`input-${name}`} 
                    placeholder="Hora salida .."
                    name="salida">
                </input>

                <input 
                    type="text" 
                    className={`input-${name}`} 
                    placeholder="Obra.."
                    name="obra">
                </input>

                <input 
                    type="text" 
                    className={`input-${name}`} 
                    placeholder="Trabajo realizado.."
                    name="trabajo">
                </input>

                <div 
                    className={`button-${name}`}
                    onClick={(e)=>{handleEnviarDatos(e,setEnvio)}}>
                    Enviar
                </div>
                {(envio) ? <Pops mensage={'Enviado correctamente..'}></Pops> : <></>}
            </form>
    )
}