import react from 'react';
import {getFormData} from '../assets/getFormData';
import {postAjax} from '../assets/postAjax';
import {PORT_BD} from '../constantes';
import {resetFormulario} from '../assets/resetFormulario';

export const handleLogin = (e,setLogin,setAuthentication,setUsuario) => {

   
    const res = getFormData(e);
    console.log(res);
    postAjax(`${PORT_BD}/login/usuarios`,res,(resData)=>{
        if (resData.login != true){
            console.log('Fallo de authentication');
            setAuthentication(resData.login);
            
        }else{
            setLogin(resData.login);
            setUsuario(resData.usuario);
        }
    });

    resetFormulario(e);

}